<header class="flex justify-between wrap py-5 mx-auto"><h1 class="text-xl uppercase font-thin tracking-widest">Tic Tac&nbsp;<span class="text-green">Vue</span></h1><Stat class="text-lg" :stat="stat"></Stat></header><section class="relative flex h-full"><div class="absolute flex justify-center items-center flex-col w-full h-full bg-black bg-opacity-70" v-if="isEndOfGame"><Stat class="flex-grow-0 text-xl" :stat="stat"></Stat><button class="flex-grow-0 text-2xl font-thin mt-8 border-2 border-white py-2 px-4 lowercase transition duration-500 hover:bg-white hover:text-black" @click="handleClick">New Game</button></div><div class="wrap mx-auto flex justify-center items-center pb-5 pt-1 h-full"><Board :cells="cells"></Board></div></section>